<template>
  <div class="string">
    <div v-for='note in orderNotesForString(openNote, allNotes)' :key='note'>
      <Note :keyOf='note'/>
    </div>
  </div>
</template>

<script>
  import Note from './Note.vue'

  export default {
    name: "String",
    props: {
      openNote: String,
    },
    components: {
      Note
    },
    data() {
      return {
        allNotes: ['C4', 'C#4', 'D4', 'D#4', 'E4', 'F4',
                   'F#4', 'G4', 'G#4', 'A4', 'A#4', 'B4']
      }
    },
    methods: {
      orderNotesForString: (startNote, collectionOfNotes) => {
        let copyNotes = [...collectionOfNotes]
        while (copyNotes[0] !== startNote) {
          copyNotes.push(copyNotes.shift())
        }
        return copyNotes
      }
    }
  }
</script>

<style scoped>
  .string {
    display: block;
    padding-bottom: 40px;
    height: 20px;
    width: 100%;

  }
</style>
