<template>
  <div v-on:click='makeSound(keyOf)' class="note">
    <p>{{keyOf}}</p>
  </div>
</template>

<script>
  import { Synth } from 'tone'

  export default {
    name: "Note",
    props: {
      keyOf: String
    },
    methods: {
      makeSound: (key) => {
        const synth = new Synth
        synth.toMaster();
        synth.triggerAttackRelease(`${key}`, '5n');
      }
    }
  }
</script>

<style scoped>
  .note {
    border: 2px solid black;
    display: inline-block;
    float: left;
    width: 50px;
    text-align: center;
  }
</style>
